.choose {
    &__container {
        @include flex-column;
        align-items: center;
    }

    &__title {
        text-align: center;
    }

    &__content {
        @include flex-all-sb;
        gap: 2rem;
        margin-top: 6rem;
    }

    &__item {
        @include flex-column;
        align-items: center;
        &-top {
            padding: 5.4rem;
            border-radius: 50%;
            background: #1aa09028;
        }
        &-img {
            opacity: 1;
        }

        &-title {
            text-align: center;
            margin-top: 3.6rem;
        }

        &-text {
            text-align: center;
            margin-top: 1.1rem;
        }
    }
    &__slider {
        width: 100rem;
        padding-top: 28rem;
        position: relative;
        &::after {
            content: "“";
            position: absolute;
            top: 7rem;
            left: -1rem;
            color: var(--accent-color);
            font-size: 26rem;
            font-weight: 400;
            line-height: 147%;
            text-transform: capitalize;
        }
    }

    &__slide-text {
        font-size: 3.2rem;
        font-weight: 400;
        line-height: 157%;
        text-transform: capitalize;
        color: var(--light-color);
        max-width: 100rem;
        
    }

    &__slide-bottom {
        @include flex-column;
        margin-top: 4rem;
    }

    &__slide-name {
        font-size: 2.2rem;
        font-weight: 600;
        line-height: 126%;
        letter-spacing: 0.22px;
        text-transform: capitalize;
        color: var(--light-color);
    }

    &__prof {
        font-size: 1.8rem;
        font-style: normal;
        font-weight: 400;
        line-height: 170%;
        color: var(--light-color2);
    }

    &__arrow--prev {
        position: absolute;
        right: 8.5rem;
        bottom: .35rem;
        padding: 2rem 3rem;
        background-color: var(--bg-color);
        transform: rotate(180deg);
        z-index: 4;
        &::before {
            content: "";
            position: absolute;
            left: .2rem;
            top: .2rem;
            width: 1px;
            height: 61px;
            background-color: #353F49;
        }

    }

    &__arrow--next {
        position: absolute;
        right: -.3rem;
        bottom: 0px;
        padding: 2rem 3rem;
        background-color: var(--bg-color);
        z-index: 4;
    }
}
