.residence {
    position: relative;
    z-index: 1;
    background: url(../img/residence-bg.png) no-repeat;
    background-size: cover;
    background-color: rgba(255, 255, 255, 0.050);
    &__container {
    }

    &__title {
    }

    &__slider {
        margin-top: 4rem;
    }

    &__wrapper {
    }

    &__slide {
        @include flex-column;
    }

    &__img {
    }

    &__name {
        margin-top: 2.2rem;
    }

    &__geo {
        margin-top: 0.3rem;
    }

    &__descr {
        @include flex-align-center;
        gap: 2rem;
        margin-top: 1.6rem;
    }

    &__item {
        @include flex-align-center;
        gap: 0.4rem;
    }

    &__item-text {
        color: var(--light-color);
        font-size: 1.6rem;
        font-weight: 400;
        text-transform: capitalize;
    }

    &__arrow--next {
        position: absolute;
        padding: 2.8rem 3.3rem;
        right: 14rem;
        top: 46%;
        border-radius: 50%;
        border: 0.1rem solid var(--grey-color);
        z-index: 5;

        @include small-desktop {
            display: none;
        }
    }

    &__arrow--prev {
        position: absolute;
        left: 14rem;
        top: 46%;
        padding: 2.8rem 3.3rem;
        border-radius: 50%;
        border: 0.1rem solid var(--grey-color);
        z-index: 5;
        transform: rotate(180deg);

        @include small-desktop {
            display: none;
        }
    }

    &__bottom {
        @include flex-all-center;
        margin-top: 6.8rem;
    }

    &__btn {
        font-size: 2.4rem;
        padding: 2.2rem 4.2rem;
    }
}
