.footer {
  position: relative;
  hr {
    position: absolute;
    bottom: 5.5rem;
    right: 0px;
    width: 100%;
    border: 1px solid var(--grey-color2);
  }
  &__container {
  }

  &__body {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  &__left {
  }

  &__logo {
  }

  &__img {
  }

  &__text {
    margin-top: 2.2rem;
    max-width: 34rem;
  }

  &__socials {
    @include flex-align-center;
    gap: 3.2rem;
    margin-top: 3.2rem;
  }

  &__social {
  }

  &__link {
    svg {
      @media (any-hover: hover) {
        &:hover {
          path {
            fill: var(--light-color);
          }
        }
      }
      path {
        fill: #555e68;
        transition: 300ms all;
      }
    }
  }

  &__list {
  }

  &__item {
  }

  &__item-title {
    margin-bottom: 0.5rem;
  }

  &__item-link {
  }

  &__copyright {
    @include flex-all-sb;
    margin-top: 9.7rem;
    padding-block: 1.5rem;
  }

  &__copyright-text {
  }
}
