.text {
  &-small {
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 170%;
    color: var(--light-color2);
  }

  &-large {
    font-size: 2.6rem;
    font-weight: 600;
    line-height: 126%;
    letter-spacing: 0.26px;
    text-transform: capitalize;
    color: var(--light-color);
  }
}

.title {
  color: var(--light-color);
  font-size: 4.8rem;
  font-weight: 600;
  line-height: 126%;
  text-transform: capitalize;
}
