.header {
    position: fixed;
    width: 100%;
    z-index: 100;
    background-color: var(--bg-color);
    padding: 2rem 0;
    &__container {
        @include flex-all-sb;
    }

    &__logo {
        max-width: 20.0rem;
    }

    &__right {
        @include flex-align-center;
        gap: 7.4rem;
    }

    &__buttons {
        @include flex-align-center;
        gap: .5rem;
    }

    &__btn {
    }
}
.login {
    background: transparent;
    @media (any-hover: hover) {
        &:hover {
            background-color: var(--accent-color);
        }
    }
}
.sign-up {
    padding: 1.2rem 3.3rem;

    @media (any-hover: hover) {
        &:hover {
            background-color: transparent;
        }
    }
}
