.nav {
  @include small-tablet {
    display: none;
  }

  &__list {
    @include flex-align-center;
    gap: 2.3rem;
  }

  &__item {
  }

  &__img {
  }

  &__link {
    @include flex-align-center;
    position: relative;
    color: var(--light-color2);
    font-size: 1.6rem;
    font-weight: 500;
    transition: all 300ms;
    text-transform: capitalize;

    &::before{
      content: "";
      position: absolute;
      left: 0;
      bottom: -.4rem;
      width: 100%;
      height: 0.3rem;
      background: var(--accent-color);
      opacity: 0;
      transition: all 300ms;
    }

    @media (any-hover: hover) {
      &:hover {
        color: var(--light-color);
        
        &::before{
          opacity: 1;
        }
      }
    }
  }
}
