@charset "UTF-8";
:root {
  --container-width: 142rem;
  --container-padding: 1.5rem;
}

:root {
  --light-color: #ffffff;
  --light-color2: #D6D6D6;
  --grey-color: #A9ADB1;
  --grey-color2: #303A44;
  --bg-color: #121B25;
  --bg-color2: linear-gradient(270deg, #1A242F 0.01%, rgba(26, 36, 47, 0.00) 82.97%);
  --bg-color3: #1A242F;
  --accent-color: #1AA090;
}

:root {
  --section-offset: 18rem;
}
@media (max-width: 576px) {
  :root {
    --section-offset: 2rem;
  }
}

@font-face {
  font-family: "WorkSans";
  src: url("../fonts/WorkSansRegular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "WorkSans";
  src: url("../fonts/WorkSansMedium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "WorkSans";
  src: url("../fonts/WorkSansSemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
html {
  font-family: "WorkSans", sans-serif;
  font-size: 0.5208333333vw;
  background: var(--bg-color);
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}
@media (max-width: 1440px) {
  html {
    font-size: 0.703125vw;
  }
}
@media (max-width: 1024px) {
  html {
    font-size: 0.9375vw;
  }
}
@media (max-width: 768px) {
  html {
    font-size: 1.25vw;
  }
}
@media (max-width: 576px) {
  html {
    font-size: 1.7142857143vw;
  }
}
@media (max-width: 420px) {
  html {
    font-size: 2.25vw;
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  overflow-x: hidden;
}
.body--fixed {
  overflow-y: hidden;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

ul {
  padding-left: 0px;
  list-style: none;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

button {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

input {
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

[class*=__container] {
  max-width: calc(var(--container-width) + var(--container-padding) * 2);
  padding: 0 var(--container-padding);
  margin: 0 auto;
}

.hidden {
  font-size: 0;
  opacity: 0;
  visibility: hidden;
}

.button-1 {
  display: inline-block;
  padding: 1.2rem 4.1rem;
  color: var(--light-color);
  font-size: 1.6rem;
  font-weight: 500;
  text-transform: capitalize;
  border: 0.1rem solid var(--accent-color);
  background: var(--accent-color);
  transition: 200ms all;
}
@media (any-hover: hover) {
  .button-1:hover {
    background-color: transparent;
  }
}
.button-1:active {
  background-color: transparent;
}

.button-2 {
  display: inline-block;
  font-weight: 700;
  font-size: 1.6rem;
  line-height: 2rem;
  padding: 1rem 6rem;
  color: var(--light-color);
  background: var(--violet-secondary-color);
  border-radius: 0.6rem;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.07);
  transition: all 600ms;
}
.button-2:hover {
  background: var(--violet-color);
}
.button-2:active {
  background: var(--accent-color);
}

.section {
  padding: var(--section-offset) 0;
}

.section-top {
  padding: var(--section-offset) 0 0 0;
}

.text-small {
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 170%;
  color: var(--light-color2);
}
.text-large {
  font-size: 2.6rem;
  font-weight: 600;
  line-height: 126%;
  letter-spacing: 0.26px;
  text-transform: capitalize;
  color: var(--light-color);
}

.title {
  color: var(--light-color);
  font-size: 4.8rem;
  font-weight: 600;
  line-height: 126%;
  text-transform: capitalize;
}

.header {
  position: fixed;
  width: 100%;
  z-index: 100;
  background-color: var(--bg-color);
  padding: 2rem 0;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  max-width: 20rem;
}
.header__right {
  display: flex;
  align-items: center;
  gap: 7.4rem;
}
.header__buttons {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.login {
  background: transparent;
}
@media (any-hover: hover) {
  .login:hover {
    background-color: var(--accent-color);
  }
}

.sign-up {
  padding: 1.2rem 3.3rem;
}
@media (any-hover: hover) {
  .sign-up:hover {
    background-color: transparent;
  }
}

@media (max-width: 768px) {
  .nav {
    display: none;
  }
}
.nav__list {
  display: flex;
  align-items: center;
  gap: 2.3rem;
}
.nav__link {
  display: flex;
  align-items: center;
  position: relative;
  color: var(--light-color2);
  font-size: 1.6rem;
  font-weight: 500;
  transition: all 300ms;
  text-transform: capitalize;
}
.nav__link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -0.4rem;
  width: 100%;
  height: 0.3rem;
  background: var(--accent-color);
  opacity: 0;
  transition: all 300ms;
}
@media (any-hover: hover) {
  .nav__link:hover {
    color: var(--light-color);
  }
  .nav__link:hover::before {
    opacity: 1;
  }
}

.hero__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
}
.hero__content {
  max-width: 70rem;
}
.hero__title {
  font-size: 6.8rem;
  font-weight: 600;
  line-height: 126%;
  color: var(--light-color);
  text-transform: capitalize;
}
.hero__text {
  margin-top: 1.8rem;
}
.featured__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1.5rem;
  margin-top: 40px;
}

.residence {
  position: relative;
  z-index: 1;
  background: url(../img/residence-bg.png) no-repeat;
  background-size: cover;
  background-color: rgba(255, 255, 255, 0.05);
}
.residence__slider {
  margin-top: 4rem;
}
.residence__slide {
  display: flex;
  flex-direction: column;
}
.residence__name {
  margin-top: 2.2rem;
}
.residence__geo {
  margin-top: 0.3rem;
}
.residence__descr {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-top: 1.6rem;
}
.residence__item {
  display: flex;
  align-items: center;
  gap: 0.4rem;
}
.residence__item-text {
  color: var(--light-color);
  font-size: 1.6rem;
  font-weight: 400;
  text-transform: capitalize;
}
.residence__arrow--next {
  position: absolute;
  padding: 2.8rem 3.3rem;
  right: 14rem;
  top: 46%;
  border-radius: 50%;
  border: 0.1rem solid var(--grey-color);
  z-index: 5;
}
@media (max-width: 1440px) {
  .residence__arrow--next {
    display: none;
  }
}
.residence__arrow--prev {
  position: absolute;
  left: 14rem;
  top: 46%;
  padding: 2.8rem 3.3rem;
  border-radius: 50%;
  border: 0.1rem solid var(--grey-color);
  z-index: 5;
  transform: rotate(180deg);
}
@media (max-width: 1440px) {
  .residence__arrow--prev {
    display: none;
  }
}
.residence__bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 6.8rem;
}
.residence__btn {
  font-size: 2.4rem;
  padding: 2.2rem 4.2rem;
}

.choose__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.choose__title {
  text-align: center;
}
.choose__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
  margin-top: 6rem;
}
.choose__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.choose__item-top {
  padding: 5.4rem;
  border-radius: 50%;
  background: rgba(26, 160, 144, 0.1568627451);
}
.choose__item-img {
  opacity: 1;
}
.choose__item-title {
  text-align: center;
  margin-top: 3.6rem;
}
.choose__item-text {
  text-align: center;
  margin-top: 1.1rem;
}
.choose__slider {
  width: 100rem;
  padding-top: 28rem;
  position: relative;
}
.choose__slider::after {
  content: "“";
  position: absolute;
  top: 7rem;
  left: -1rem;
  color: var(--accent-color);
  font-size: 26rem;
  font-weight: 400;
  line-height: 147%;
  text-transform: capitalize;
}
.choose__slide-text {
  font-size: 3.2rem;
  font-weight: 400;
  line-height: 157%;
  text-transform: capitalize;
  color: var(--light-color);
  max-width: 100rem;
}
.choose__slide-bottom {
  display: flex;
  flex-direction: column;
  margin-top: 4rem;
}
.choose__slide-name {
  font-size: 2.2rem;
  font-weight: 600;
  line-height: 126%;
  letter-spacing: 0.22px;
  text-transform: capitalize;
  color: var(--light-color);
}
.choose__prof {
  font-size: 1.8rem;
  font-style: normal;
  font-weight: 400;
  line-height: 170%;
  color: var(--light-color2);
}
.choose__arrow--prev {
  position: absolute;
  right: 8.5rem;
  bottom: 0.35rem;
  padding: 2rem 3rem;
  background-color: var(--bg-color);
  transform: rotate(180deg);
  z-index: 4;
}
.choose__arrow--prev::before {
  content: "";
  position: absolute;
  left: 0.2rem;
  top: 0.2rem;
  width: 1px;
  height: 61px;
  background-color: #353F49;
}
.choose__arrow--next {
  position: absolute;
  right: -0.3rem;
  bottom: 0px;
  padding: 2rem 3rem;
  background-color: var(--bg-color);
  z-index: 4;
}

.find-home {
  background: var(--bg-color2);
}
.find-home__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 6.3rem;
}
.find-home__images {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
.find-home__content {
  max-width: 63.7rem;
}
.find-home__text {
  margin-top: 2.2rem;
}
.find-home__btn {
  font-size: 2.4rem;
  padding: 2.2rem 4.2rem;
  margin-top: 4.5rem;
}

.simply__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8.8rem;
}
.simply__title {
  max-width: 51.2rem;
  font-size: 6.8rem;
}
.simply__text {
  max-width: 63.7rem;
}
.simply__btn {
  font-size: 2.4rem;
  font-weight: 500;
  padding: 2.2rem 4.2rem;
  margin-top: 3.6rem;
}

.footer {
  position: relative;
}
.footer hr {
  position: absolute;
  bottom: 5.5rem;
  right: 0px;
  width: 100%;
  border: 1px solid var(--grey-color2);
}
.footer__body {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.footer__text {
  margin-top: 2.2rem;
  max-width: 34rem;
}
.footer__socials {
  display: flex;
  align-items: center;
  gap: 3.2rem;
  margin-top: 3.2rem;
}
@media (any-hover: hover) {
  .footer__link svg:hover path {
    fill: var(--light-color);
  }
}
.footer__link svg path {
  fill: #555e68;
  transition: 300ms all;
}
.footer__item-title {
  margin-bottom: 0.5rem;
}
.footer__copyright {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 9.7rem;
  padding-block: 1.5rem;
}